import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import * as moment from 'moment';
import { ProductService } from '../../../shared/services/product.service';
import { ApiCartService } from 'src/app/shared/api-services/cart.service';

@Component({
  selector: 'app-success',
  templateUrl: './success.component.html',
  styleUrls: ['./success.component.scss']
})
export class SuccessComponent implements OnInit {

  public orderDetails: any = null;

  constructor(
    public productService: ProductService,
    private route: ActivatedRoute,
    private apiCartService: ApiCartService,
    private router: Router
  ) { }

  ngOnInit(): void {	
    if(this.route.snapshot.data['order'] === null) {
      this.router.navigate(['/']);
    }
    this.orderDetails = this.route.snapshot.data['order'];
    this.apiCartService.reloadSessionId();
  }

  getDateExpected() {
    let businessDay;
    if(this.orderDetails.address_country == 'italy') {
      businessDay = 2;
    } else {
      businessDay = 5;
    }
    return this.addBusinessDays(moment(this.orderDetails.created_at), businessDay).format('DD/MM/YYYY');
  }

  getOrderCreatedAt() {
    return moment(this.orderDetails.created_at).format('DD/MM/YYYY');
  }

  addBusinessDays(originalDate, numDaysToAdd) {
    const Sunday = 0;
    const Saturday = 6;
    let daysRemaining = numDaysToAdd;
  
    const newDate = originalDate.clone();
  
    while (daysRemaining > 0) {
      newDate.add(1, 'days');
      if (newDate.day() !== Sunday && newDate.day() !== Saturday) {
        daysRemaining--;
      }
    }
  
    return newDate;
  }

  getFloat(value) {
    return parseFloat(value).toFixed(2);
  }

  getAmoutOff() {
    if(this.orderDetails.coupon.type === 'fixed') {
      return this.orderDetails.coupon.amount_off;
    } else if (this.orderDetails.coupon.type === 'percentage') {
      return '- ' + (this.orderDetails.coupon.amount_off * 100).toFixed(2) + ' %';
    }
  }
}
