import { ActivatedRouteSnapshot, Resolve, RouterStateSnapshot } from '@angular/router';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { ApiProductService } from '../shared/api-services/product.service';
import { ApiCartService } from '../shared/api-services/cart.service';

@Injectable({
  providedIn: 'root',
})
export class OrderResolver implements Resolve<any> {

  /**
   * Constructor
   *
   * @param {ApiCartService} apiCartService
   */
  public constructor(private apiCartService: ApiCartService) { }

  /**
   * Resolver for the game periods
   *
   * @param {ActivatedRouteSnapshot} route
   * @param {RouterStateSnapshot} state
   *
   * @return {Observable<any>}
   */
  public resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): any {
    if(route.routeConfig.path === 'checkout') {
      return this.apiCartService.getOrder().pipe(
        map((order) => {
            return order;
          }),
      );
    } else {
      return this.apiCartService.getOrder(route.params['id']).pipe(
        map((order) => {
            return order;
          }),
      );
    }
  }
}
