import { Component, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ProductDetailsMainSlider, ProductDetailsThumbSlider } from '../../../../shared/data/slider';
import { Product } from '../../../../shared/classes/product';
import { ProductService } from '../../../../shared/services/product.service';
import { SizeModalComponent } from "../../../../shared/components/modal/size-modal/size-modal.component";
import { ApiProductService } from 'src/app/shared/api-services/product.service';
import { ApiCartService } from 'src/app/shared/api-services/cart.service';
import { DomSanitizer } from '@angular/platform-browser';
const _ = require('lodash');

@Component({
  selector: 'app-product-no-sidebar',
  templateUrl: './product-no-sidebar.component.html',
  styleUrls: ['./product-no-sidebar.component.scss'],
})
export class ProductNoSidebarComponent implements OnInit {

  public product: Product;
  public relatedProducts: Product[] = [];
  public counter: number = 1;
  public uniqSize: string[] = [];
  public activeSlide: any = 0;
  public selectedSize: any = null;
  public variants: any = null;
  public selectedVariant: any = null;
  public active = 1;
  public cart: any = null;
                                                       
  @ViewChild("sizeChart") SizeChart: SizeModalComponent;

  public ProductDetailsMainSliderConfig: any = ProductDetailsMainSlider;
  public ProductDetailsThumbConfig: any = ProductDetailsThumbSlider;

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    public productService: ProductService,
    private apiProductService: ApiProductService,
    private apiCartService: ApiCartService,
    public sanitizer: DomSanitizer
  )
  {
    this.apiCartService.cart.subscribe(
      (cart) => {
        this.cart = cart;
      }
    );
  }

  ngOnInit(): void 
  {
    this.product = this.route.snapshot.data['productData']['product'];
    this.relatedProducts = this.route.snapshot.data['productData']['relatedProducts'];
    this.Size(this.product);
    this.pickUpVariants();

    this.route.paramMap.subscribe(
      (data: any) => {
        this.apiProductService.getProduct(data.params['id']).subscribe(
          (productData) => {
            this.product = productData.product;
            this.relatedProducts = productData.relatedProducts;
            this.selectedSize = null;
            this.selectedVariant = null;
            this.counter = 1;
            this.Size(this.product);
            this.pickUpVariants();
            if(this.uniqSize.length === 1) {
              this.selectSize(this.uniqSize[0])
            }
          }
        );
      }
    );
  }

  // Get Product Color
  Color(variants) {
    const uniqColor = []
    return uniqColor
  }

  pickUpVariants()
  {
    if(this.product.variants.length > 0) {
      this.variants = _.cloneDeep(this.product.variants);
      this.variants.unshift(this.product);
      this.selectVariant(this.product);
    } else {
      this.variants = [];
    }
  }

  selectVariant(variant)
  {
    this.selectedVariant = variant;
    this.selectedSize = null;
    this.counter = 1;
    this.Size(this.selectedVariant);
  }

  // Get Product Size
  Size(product) {
    this.uniqSize = [];
    const sortingArr = [ 'xs', 's', 'm', 'l', 'xl', 'xxl' ];
    
    let sortedSizes = product.sizes.sort(function(a, b) {
      return sortingArr.indexOf(a.size) - sortingArr.indexOf(b.size);
    });

    sortedSizes.forEach(
      (size) => {
        this.uniqSize.push(size)
      }
    );
  }

  selectSize(size) {
    if(size.quantity == 0) {
      return;
    }
    this.selectedSize = size.size;
    let selectedSizeData = size
    if(this.cart != null) {
      let productInCart = this.cart.products.find((product) => (product.product.id === this.product.id && product.size === this.selectedSize));        
      if(productInCart != null) {
        if((this.counter + productInCart.quantity) > selectedSizeData.quantity) {
          this.counter = selectedSizeData.quantity - productInCart.quantity;
        } else if(this.counter == 0) {
          this.counter = 1;
        }
      } else if(this.counter == 0) {
        this.counter = 1;
      }
    } else {
      if(this.counter > selectedSizeData.quantity) {
        this.counter = selectedSizeData.quantity;
      } else if(this.counter == 0) {
        this.counter = 1;
      }
    }
  }

  // Increament
  increment() {
    let productToUse = this.product;
    if(this.selectedVariant != null) {
      productToUse = this.selectedVariant;
    }
    if(this.selectedSize != null) {
      let selectedSizeData = productToUse.sizes.find((size) => size.size === this.selectedSize);
      if(selectedSizeData != null) {
        if(this.cart != null) {
          let productInCart = this.cart.products.find((product) => (product.product.id === productToUse.id && product.size === this.selectedSize));        
          if(productInCart != null) {
            if((this.counter + productInCart.quantity) < selectedSizeData.quantity) {
              this.counter++;
            }
          } else {
            if(this.counter < selectedSizeData.quantity) {
              this.counter++;
            }
          }
        } else {
          if(this.counter < selectedSizeData.quantity) {
            this.counter++;
          }
        }
      } else {
        this.counter++;
      }
    }
  }

  // Decrement
  decrement() {
    if (this.counter > 1) this.counter--;
  }

  // Add to cart
  async addToCart(event) {
    event.preventDefault();
    let product = this.product;
    if(this.selectedVariant != null) {
      product = this.selectedVariant;
    }
    this.apiCartService.addProductToShoppingCart(product, this.counter, this.selectedSize).subscribe(
      () => {
        this.router.navigate(['/shop/cart']);
      }
    );
  }
}

