import { ActivatedRouteSnapshot, Resolve, RouterStateSnapshot } from '@angular/router';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { ApiProductService } from '../shared/api-services/product.service';

@Injectable({
  providedIn: 'root',
})
export class ProductPageResolver implements Resolve<any> {

  /**
   * Constructor
   *
   * @param {ApiProductService} apiProductService
   */
  public constructor(private apiProductService: ApiProductService) { }

  /**
   * Resolver for the game periods
   *
   * @param {ActivatedRouteSnapshot} route
   * @param {RouterStateSnapshot} state
   *
   * @return {Observable<any>}
   */
  public resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): any {
    let test = this.apiProductService.getProduct(route.params['id']).pipe(
      map((productData) => {
          return productData;
        }),
  );
  
    return test;
  }
}
