import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ViewportScroller } from '@angular/common';
import { ProductService } from "../../../shared/services/product.service";
import { Product } from '../../../shared/classes/product';
import { ApiProductService } from 'src/app/shared/api-services/product.service';
import { Location } from '@angular/common'; 

@Component({
  selector: 'app-collection-no-sidebar',
  templateUrl: './collection-no-sidebar.component.html',
  styleUrls: ['./collection-no-sidebar.component.scss']
})
export class CollectionNoSidebarComponent implements OnInit {

  public grid: string = 'col-xl-4 col-6';
  public layoutView: string = 'grid-view';
  public products: Product[] = [];
  public productsToShow: Product[] = [];
  public pageNo: number = 1;
  public paginate: any = {}; // Pagination use only
  public sortBy: string; // Sorting Order
  public categories: any[] = [];
  public mainCategory: string = 'mtb';
  public subCategory: string = 'all';

  constructor(
    private route: ActivatedRoute, private router: Router,
    private viewScroller: ViewportScroller, public productService: ProductService,
    private apiProductService: ApiProductService, private location: Location
    ) {   
      this.apiProductService.products.subscribe(
        (products) => {
          this.products = products;
          this.filterProductsPerCategory();
        }
      );

      this.apiProductService.categories.subscribe(
        (categories) => {
          this.categories = categories;
        }
      );
      
      this.route.queryParams.subscribe(params => {
        if(params.main != undefined) {
          this.mainCategory = params.main;
        }
        if(params.sub != undefined) {
          this.subCategory = params.sub;
        }

        this.filterProductsPerCategory();
      });
  }

  ngOnInit(): void {
  }

  onSelectMain(newValue: string) {
    this.mainCategory = newValue;
    this.subCategory = 'all';
    this.filterProductsPerCategory();
    this.updateUrl();
  }

  onSelectSub(newValue: string) {
    this.subCategory = newValue;
    this.filterProductsPerCategory();
    this.updateUrl();
  }

  updateUrl() {
    this.location.replaceState("/shop/?main=" + this.mainCategory + '&sub=' + this.subCategory);
  }

  // SortBy Filter
  sortByFilter(value) {
    this.router.navigate([], { 
      relativeTo: this.route,
      queryParams: { sortBy: value ? value : null},
      queryParamsHandling: 'merge', // preserve the existing query params in the route
      skipLocationChange: false  // do trigger navigation
    }).finally(() => {
      this.viewScroller.scrollToAnchor('products'); // Anchore Link
    });
  }

  // product Pagination
  setPage(page: number) {
    this.router.navigate([], { 
      relativeTo: this.route,
      queryParams: { page: page },
      queryParamsHandling: 'merge', // preserve the existing query params in the route
      skipLocationChange: false  // do trigger navigation
    }).finally(() => {
      this.viewScroller.scrollToAnchor('products'); // Anchore Link
    });
  }

  filterProductsPerCategory() {
    this.productsToShow = this.products.filter(
      (product) => {
        return product.type === this.mainCategory && (this.subCategory === 'all' || product.category.name === this.subCategory);
      }
    );
  }

  getTypeCategories() {
    return this.categories.filter((category) => { return category.type === this.mainCategory });
  }

}
